import React, { Component } from 'react';
import axios from 'axios';
import './Section.scss'
import Movies from './Movies';
import Loading from './Loading'

class Section extends Component {
    state = {
        lee : [],
        isLoading:true
    }
    getMovies = async () => {
        const {data:{data:{movies}}} = await axios.get("https://yts.mx/api/v2/list_movies.json")
        this.setState({
            lee:movies
        })
    }
    componentDidMount(){
        setTimeout(() => {
            this.setState({
                isLoading:false
            })
        },5000);
        this.getMovies()
    }
    render() {
        const {lee, isLoading} = this.state;
        return (
            <>
            {
                isLoading ? <Loading /> : (
                    <section className="container cf">
                        {
                            lee.map( (movies) => (<Movies key={movies.id} year={movies.year} title={movies.title} summary={movies.summary} poster={movies.medium_cover_image} genre={movies.genres}/>) )
                        }
                    </section>
                    )
            }
            </>
        );
    }
}

export default Section;